* {
    padding: 0;
    margin: 0;
}


.stats_base {

    width: 100%;
    height: 100vh;
    background-color: rgb(255, 255, 255);

    display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column;
}





.charts {
    width: 100%;
    height: 100vh;
    background-color: green;
 

    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-rows: repeat(3, 1fr);
    gap: 5%;
    place-items: center;

    padding: 5%;

}




.line{
    width: 100%;
    height: 15vh;
    /* background-color: red; */
    margin-top: 8%;

    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5%;
    padding-left: 5%;
    padding-right: 5%;
}






.chart {
    width: 100%;
    height: 100%;
    /* background-color: rgb(231, 231, 231); */
    overflow: hidden;

    border-radius: 1rem;

    display: flex;
    align-items: center;
    justify-content: center;
    /* padding: 5%; */
}




.four {
    grid-column: 2/3;
    grid-row: 2/4;
    background-color: rgb(255, 214, 119);
}



.actual_bar {
    width: 100%;
    height: 100%;
    /* background-color: red; */
}




.one{
    background-color: rgb(143, 87, 255);
   
}

.two{
    background-color: skyblue;
}

.three{
    background-color: orange;
}

.five{
    background-color: rgb(46, 128, 46);
}





.ubox{
    width: 100%;
    height: 90%;
    background-image: linear-gradient(to right, rgb(255, 177, 32) , rgb(255, 230, 156));

    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 1rem;

    
}






.l{
    width: 80%;
    height: 100%;
    /* background-color: aqua; */
    border-radius: 1rem;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 5%;
    padding: 5%;
    color: white;

}


.r{
    width: 20%;
    height: 100%;
    /* background-color: blanchedalmond; */
    border-radius: 1rem;

    display: flex;
    align-items: center;
    justify-content: center;

}


.fit{
    width: 100%;
    height: 50%;
}


